\n  grid-template-rows: 100px repeat(auto-fit, 4.5rem);\n  width: 100%;\n}\n\n.main-content > h2 {\n  font-size: 2rem;\n  grid-row: 1 / 2;\n  grid-column: 1 / 2;\n  justify-self: center;\n  margin-top: 0.5rem;\n}\n\n.main-content > button {\n  grid-column: 1 / 2;\n  grid-row: 1 / 2;\n  height: 30px;\n  width: 80px;\n  justify-self: center;\n  align-self: flex-end;\n  border-radius: 0.5rem;\n  background-color: var(--side-panel-bg-color);\n  font-weight: bold;\n}\n\n#task > input[type=\"text\"] {\n  grid-column: 2 / 3;\n  position: absolute;\n  border-radius: 0.2rem;\n  padding-left: 0.5rem;\n}\n#task > input[type=\"date\"] {\n  grid-column: 3/4;\n  position: absolute;\n  right: 70px;\n}\n\n#task > h3 {\n  width: max-content;\n  cursor: pointer;\n  align-self: center;\n  position: relative;\n}\n\n#task > #taskDate {\n  width: max-content;\n  cursor: pointer;\n}\n\n#task > #taskDeleteButton {\n  height: 24px;\n  cursor: pointer;\n}\n\n/* Fixes a bug where the click event fires on children of deletebutton */\n#taskDeleteButton > * {\n  pointer-events: none;\n}\n\n#task {\n  background: rgb(255, 255, 255);\n  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(250, 250, 250, 1) 42%);\n  border-radius: 0.5rem;\n  margin: 0.5rem 4rem;\n  border: 1px solid rgba(128, 128, 128, 0.1);\n  display: grid;\n  grid-template-columns: 1em auto max-content max-content;\n  padding: 0 0.5rem;\n  align-items: center;\n  gap: 10px;\n  height: 4rem;\n  transition: all linear 0.5sec;\n}\n\n#task:hover {\n  background-color: rgba(228, 228, 228, 0.462);\n}\n\n.hide {\n  display: none;\n}\n\n#addTaskButton {\n  grid-row: 1 / 2;\n  grid-column: 1 / 2;\n  height: 50px;\n  width: 50px;\n  background-color: var(--side-panel-bg-color);\n  color: var(--side-panel-items-color);\n  font-size: 3rem;\n  border-radius: 50%;\n  margin: 1rem;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border: 1px solid rgb(216, 216, 216);\n}\n\n#addTaskButton:hover {\n  border: 1px solid rgb(121, 121, 121);\n  background-color: rgb(149, 149, 149);\n  color: white;\n}\n\n#addTaskButton:active {\n  border: 1px solid rgb(216, 216, 216);\n  background-color: var(--side-panel-bg-color);\n  color: var(--side-panel-items-color);\n}\n\n.todo-item {\n  background-color: var(--side-panel-bg-color);\n  width: 80%;\n  height: auto;\n  border-radius: 0.5rem;\n  box-shadow: 3px 3px 9px 0px rgba(0, 0, 0, 0.5);\n  -webkit-box-shadow: 3px 3px 9px 0px rgba(0, 0, 0, 0.5);\n  -moz-box-shadow: 3px 3px 9px 0px rgba(0, 0, 0, 0.5);\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  grid-template-rows: repeat(3, 1fr);\n  color: var(--side-panel-items-color);\n  padding: 0.5rem;\n  margin: 0 auto 0 auto;\n  overflow: hidden;\n  margin-top: 0.5rem;\n}\n\n.todo-item:hover {\n  background-color: rgb(255, 255, 255);\n}\n\n.todo-item > h3 {\n  color: black;\n  grid-column: 1 / 2;\n  grid-row: 1 / 2;\n}\n\n.todo-item > p:nth-child(2) {\n  grid-column: 1 / 2;\n  grid-row: 2 / 4;\n}\n\n.todo-item > p:nth-child(3) {\n  grid-column: 2 / 3;\n  grid-row: 1 / 2;\n  text-align: end;\n}\n\n.todo-item > p:nth-child(4) {\n  grid-column: 2 / 3;\n  grid-row: 2 / 3;\n  text-align: end;\n}\n\n.todo-item > p:nth-child(5) {\n  height: 24px;\n  width: 24px;\n  grid-column: 2 / 3;\n  grid-row: 3 / 4;\n  margin-left: 80%;\n  transition: all 0.5s ease;\n  -moz-transition: all 0.5s ease;\n  -webkit-transition: all 0.5s ease;\n}\n\n.todo-item > p:nth-child(5):hover {\n  transform: scale(1.1);\n  color: rgb(49, 49, 49);\n}\n\n.todo-item > p:nth-child(5):active {\n  font-size: 1.1em;\n  color: rgb(179, 179, 179);\n}\n\n.todo-item > p:nth-child(6) {\n  width: 24px;\n  height: 24px;\n  grid-column: 2 / 3;\n  grid-row: 3 / 4;\n  margin-left: 90%;\n  transition: all 0.5s ease;\n  -moz-transition: all 0.5s ease;\n  -webkit-transition: all 0.5s ease;\n}\n\n.todo-item > p:nth-child(6):hover {\n  transform: scale(1.1);\n  color: rgb(49, 49, 49);\n}\n\n.todo-item > p:nth-child(6):active {\n  font-size: 1.1em;\n  color: rgb(179, 179, 179);\n}\n\n.todo-item > p > svg {\n  fill: green;\n}\n\n.form-container {\n  --form-height: 300px;\n  --form-width: 625px;\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  grid-template-rows: repeat(2, 1fr);\n  position: absolute;\n  background-color: var(--side-panel-bg-color);\n  height: var(--form-height);\n  width: var(--form-width);\n  top: 200px;\n  right: calc(100vw / 2 - calc(var(--form-width) / 2));\n  border-radius: 0.5rem;\n  box-shadow: 8px 7px 14px 0px rgba(133, 133, 133, 0.75);\n  -webkit-box-shadow: 8px 7px 14px 0px rgba(133, 133, 133, 0.75);\n  -moz-box-shadow: 8px 7px 14px 0px rgba(133, 133, 133, 0.75);\n}\n\nform {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  grid-template-rows: 0.5fr 1fr 1fr;\n  grid-row: 1 / 3;\n  grid-column: 1 / 3;\n}\n\nform > h2 {\n  margin: 1rem 1rem;\n  grid-column: 1 / 2;\n  grid-row: 1 / 2;\n  color: var(--side-panel-items-color);\n}\n\nform > input[type=\"text\"] {\n  font-size: 1.2rem;\n  border-radius: 0.5rem;\n  border: none;\n  padding-left: 1rem;\n  width: 300px;\n  height: 50px;\n  grid-column: 1 / 2;\n  grid-row: 2 / 3;\n  margin-left: 1rem;\n}\n\nform > input[type=\"text\"]:hover {\n  border: 1px solid rgba(128, 128, 128, 0.8);\n}\n\nform > textarea {\n  resize: none;\n  grid-column: 1 / 2;\n  grid-row: 3 / 4;\n  height: 150px;\n  margin: 0 0 1rem 1rem;\n  border: none;\n  padding: 1rem 0 0 1rem;\n}\n\nform > textarea:hover {\n  border: 1px solid rgba(128, 128, 128, 0.8);\n}\n\nform > label {\n  grid-column: 2 / 3;\n  grid-row: 1 / 2;\n  margin: 1.5rem 0 0 3.5rem;\n  color: var(--side-panel-items-color);\n}\n\nform > input[type=\"date\"] {\n  padding-left: 1rem;\n  border-radius: 0.5rem;\n  border: none;\n  grid-column: 2 / 3;\n  grid-row: 1 / 2;\n  height: 35px;\n  width: 150px;\n  margin: 1rem 0 0 8rem;\n}\n\nform > input[type=\"date\"]:hover {\n  border: 1px solid rgba(128, 128, 128, 0.8);\n}\n\nform > input[name=\"urgency\"] {\n  border-radius: 0.5rem;\n  border: none;\n  padding-left: 1rem;\n  grid-column: 2 / 3;\n  grid-row: 2 / 3;\n  height: 35px;\n  width: 150px;\n  margin: 0rem 0 0 8rem;\n}\n\nform > input[name=\"urgency\"]:hover {\n  border: 1px solid rgba(128, 128, 128, 0.8);\n}\n\nform > input[name=\"parentProject\"] {\n  grid-column: 2/3;\n  grid-row: 2 / 3;\n  border-radius: 0.5rem;\n  border: none;\n  padding-left: 1rem;\n  height: 35px;\n  width: 150px;\n  margin: 3rem 0 0 8rem;\n}\n\nform > input[name=\"parentProject\"]:hover {\n  border: 1px solid rgba(128, 128, 128, 0.8);\n}\n\n#submit-button {\n  grid-column: 2 / 3;\n  grid-row: 3 / 4;\n  height: 35px;\n  width: 150px;\n  margin: 3rem 0 0 8rem;\n  background-color: var(--header-bg-color);\n  color: white;\n  font-size: 1.1rem;\n  border-radius: 0.5rem;\n}\n\n#submit-button:hover {\n  background-color: rgb(59, 180, 93);\n}\n\nform > input[type=\"button\"] {\n  grid-column: 2 / 3;\n  grid-row: 3 / 4;\n  height: 35px;\n  width: 150px;\n  margin: 6rem 0 0 8rem;\n  color: black;\n  font-size: 1.1rem;\n  border-radius: 0.5rem;\n  background-color: whitesmoke;\n}\n\nform > input[type=\"button\"]:hover {\n  background-color: rgb(180, 48, 48);\n}\n\n.project-input {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n#projectInputField {\n  font-size: 1rem;\n  height: 2rem;\n  width: 100%;\n  margin: 0.5rem 0 0.5rem 0;\n  padding: 0 0 0 1rem;\n}\n\n.side-panel > ul:nth-child(1) {\n  margin-top: 2rem;\n  height: 25%;\n}\n\n.side-panel > ul > li {\n  margin-top: 0.5rem;\n}\n"],"sourceRoot":""}]);
